import React, { useState, useRef, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import { apiGet } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";
import { isEmpty } from "../helpers";

function Accordian({ introData, faqImage, backgroundColor }) {
  const [activeIndex, setActiveIndex] = useState(0); // First accordion is open by default
  const [accordianData, setAccordionData] = useState([]); // State to hold FAQ data
  const [indexNo, setIndexNo] = useState(0);
  const contentRef = useRef([]); // Reference to store accordion content height

  const fetchFAQData = async () => {
    try {
      const response = await apiGet(API_ENDPOINTS.FAQS);
      if (response && response.success && response.data) {
        setAccordionData(response.data); // Set the FAQ data
      } else {
        console.error("No data found in the API response.");
      }
    } catch (error) {
      console.error("Error fetching FAQ data:", error);
    }
  };

  useEffect(() => {
    fetchFAQData();

    // Ensure the first accordion is open by default when the component mounts
    // if (contentRef.current[0]) {
    //   contentRef.current[0].style.height = `${contentRef.current[0].scrollHeight}px`;
    // }
  }, []); // Empty dependency array to run only on mount

  const handleToggle = (index) => {
    // Set activeIndex to the clicked accordion's index; if it's already open, set it to null
    setActiveIndex((prevIndex) => (prevIndex === index ? null : index));
    setIndexNo(index);
  };

  const getHeight = (index) => {
    // Return the height if it's the active accordion, otherwise set height to 0
    return activeIndex === index && contentRef.current[index]
      ? `${contentRef.current[index].scrollHeight}px`
      : "0px";
  };

  return (
    <div className={`HomeFaq ${backgroundColor}`}>
      <Container>
        <Row>
          <Col lg={12}>
            <div className="HomeFaq__wrp">
              {/* Conditionally render the image */}
              {faqImage && (
                <div className="HomeFaq__left">
                  <img src={faqImage} alt="FAQ" />
                </div>
              )}

              <div className={faqImage ? "HomeFaq__right" : "HomeFaq__right w-full"}>
                <SecIntro
                  titleStart={introData?.titleStart}
                  highlight={introData?.highlight}
                  titleEnd={introData?.titleEnd}
                  theme={introData?.theme}
                />

                {/* Accordion logic directly in this component */}
                <div className="accordian accordian__singleOpen md:mt-[50px] lg:mt-[90px]">
                  {!isEmpty(accordianData) ? (
                    accordianData.map((item, index) => (
                      <div
                        key={item.id} // Use item.id as the key
                        className={`accordian__row flex flex-col py-[25px] ${activeIndex === index ? "active" : ""}`}
                      >
                        <div
                          className="accordian__title flex items-start md:items-center justify-between md:flex-row flex-col md:gap-[0px] gap-[16px]"
                          onClick={() => handleToggle(index)}
                        >
                          <h4>{item.question}</h4> {/* Changed to item.question */}
                        </div>

                        {/* Accordion Content with Slide Effect */}
                        <div
                          className={`accordian__desc ${activeIndex === index ? 'openTab' : 'closeTab'}`}
                          ref={(el) => (contentRef.current[index] = el)}
                          style={{
                            //height: getHeight(index), // Dynamically set height
                            overflow: "hidden", // Prevent content from overflowing
                            transition: "height 0.4s ease", // Smooth slide-down animation
                          }}
                        >
                          <div dangerouslySetInnerHTML={{ __html: item.answer }} /> {/* Changed to item.answer */}
                        </div>
                      </div>
                    ))
                  ) : (
                    <p>No data</p>
                  )}
                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Accordian;
