import React from "react";

function SecIntro({ titleStart, highlight, titleEnd, buttonText, buttonLink, theme, buttonColor }) {
  const secIntroClass = `secIntro ${theme === "light" ? "light" : ""}`;
  const buttonClass = `btn btn-${buttonColor}`;

  return (
    <div className={secIntroClass}>
      <div className="secIntro__left">
        <h2>
          {titleStart} <span>{highlight}</span> {titleEnd}
        </h2>
      </div>
      
      {buttonText && buttonLink ? (
        <div className="secIntro__btn">
          <a href={buttonLink} className={buttonClass}>
            {buttonText}
          </a>
        </div>
      ) : null}
    </div>
  );
}

export default SecIntro;
