import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import PageTopBanner from "./PageTopBanner";
import Accordion from "./Accordian";
import DescriptionBlock from "./DescriptionBlock";

const introData = {
  titleStart: "How many driving lessons do I need? ",
  highlight: "",
  titleEnd: "",
  theme: "",
};

const contentData = {
  bottomDesc: [
    "The amount of driving lessons a learner driver needs will vary. Upon completing your first lesson with an instructor, they will be in the best position to assess how many lessons you may need.",
    "<br>",
    "<strong>This can depend on a variety of factors, such as:</strong> ",
    "<br>",
    "<ul><li>Any previous driving experiences</li><li>How quickly you pickup and learn driving techniques</li><li>Your confidence levels</li></ul>",
  ],
};

const faqData = [
  {
    title: "No experience",
    paragraphs: [
      "If you have no driving experience at all, we recommend at least 6 - 10 hours of professional driving lessons with a driving instructor. However, this is a guide, it is not uncommon to need 20 hours of instruction.",
      "Remember, everyone learns differently, and there is no set 'normal amount' of lessons required. Your instructor will teach you everything you need to know to master driving techniques and make you a safe driver on the road.",
    ],
  },
  {
    title: "Experienced Learners",
    paragraphs: [
      "If you have some driving experience with friends, family for example, you generally may need fewer professional lessons. Generally, we recommend around 5 – 8 lessons to help you prepare for your test, and be ready to be on your own on the road.",
    ],
  },
  {
    title: "International Licence Holder",
    paragraphs: [
      "If you have no driving experience at all, we recommend at least 6 - 10 hours of professional driving lessons with a driving instructor. However, this is a guide, it is not uncommon to need 20 hours of instruction.",
      "Remember, everyone learns differently, and there is no set 'normal amount' of lessons required. Your instructor will teach you everything you need to know to master driving techniques and make you a safe driver on the road.",
    ],
  },
  {
    title: "International Licence Holder",
    paragraphs: [
      "If you have no driving experience at all, we recommend at least 6 - 10 hours of professional driving lessons with a driving instructor. However, this is a guide, it is not uncommon to need 20 hours of instruction.",
      "Your instructor will teach you everything you need to know to master driving techniques and make you a safe driver on the road.",
    ],
  },
];

const HomeFaqIntroData = {
  titleStart: "Other frequently asked questions",
};

function Faq() {
  return (
    <>
      <PageTopBanner title="Frequently Asked Questions" />
      <div className="Faq bg-white">
        <Container>
          <Row>
            <Col lg={12}>
              {/* section intro block */}
              <SecIntro
                titleStart={introData.titleStart}
                highlight={introData.highlight}
                titleEnd={introData.titleEnd}
                theme={introData.theme}
              />
              <DescriptionBlock content={contentData.bottomDesc} />

              <div className="Faq__cardWrp">
                {faqData.map((faq, index) => (
                  <div key={index} className="Faq__card">
                    <h3>{faq.title}:</h3>
                    {faq.paragraphs.map((paragraph, pIndex) => (
                      <p key={pIndex}>{paragraph}</p>
                    ))}
                  </div>
                ))}
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <div className="titleCenter">
        <Accordion introData={HomeFaqIntroData} backgroundColor="grey-bg" />
      </div>
    </>
  );
}

export default Faq;
